import React, { useState, useRef, createRef, useEffect } from 'react';
import MonacoEditor from '@monaco-editor/react';
// import html2canvas from "html2canvas";

function Editor() {
  const [theme, setTheme] = useState('light');
  const [language, setLanguage] = useState('javascript');
  const [isEditorReady, setIsEditorReady] = useState(false);
  const [code, setCode] = useState('');
  const valueGetter = useRef();
  const canvasCollection = useRef([]);
  const counts = useRef(0);
  const totalCount = useRef(0);
  const actualString = useRef('');
  const html2canvas = require("html2canvas");
  const editorRef = React.createRef();

  function handleEditorDidMount(_valueGetter) {
    setIsEditorReady(true);
    valueGetter.current = _valueGetter;
  }

  useEffect(() => {
    console.log('counts.current', counts.current, totalCount.current);
    if (counts.current > 0 && counts.current < totalCount.current) {
      counts.current = counts.current + 1;
      const stringArr = actualString.current.split(/\n/g);
      console.log(counts.current);
      const newString = stringArr.splice(0, counts.current).join('\n');
      console.log(newString, editorRef);
      html2canvas(editorRef.current).then(canvas => {
        canvasCollection.current.push(canvas);
        console.log(canvasCollection.current);
        setCode(newString);
      });
    }
  });

  function handleShowValue() {
    console.log(valueGetter.current());
    counts.current = 1;
    const stringToModify = actualString.current = valueGetter.current();
    const stringArr = stringToModify.split(/\n/g);
    totalCount.current = stringArr.length;
    const newString = stringArr.splice(0, counts.current).join('\n');
    console.log(newString);
    setCode(newString);
  }
  
  function toggleTheme() {
    setTheme(theme === 'light' ? 'dark' : 'light');
  }
  
  function toggleLanguage() {
    setLanguage(language === 'javascript' ? 'python' : 'javascript');
  }

  return (
    <>
      <button onClick={toggleTheme} disabled={!isEditorReady}>Toggle theme</button>
      <button onClick={toggleLanguage} disabled={!isEditorReady}>Toggle language</button>
      <button onClick={handleShowValue} disabled={!isEditorReady}>Show value</button>
      <div ref={editorRef}>
        <MonacoEditor
          height={'90vh'} // By default, it fully fits with its parent
          theme={theme}
          language={language}
          value={code}
          editorDidMount={handleEditorDidMount}
          loading={'Loading...'}
        />
      </div>
    </>
  );
}

export default Editor;